<template>
  <div class="explorer">
    <el-form
      :inline="true"
      :model="query"
      class="explorer-form-inline"
      style="text-align: left"
    >
      <el-form-item label="链名称">
        <el-input
          v-model="query.keyword"
          placeholder="输入链名称或关键字进行过滤"
          @input="queryForm"
          style="width:300px"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="queryForm">查询</el-button>
      </el-form-item>
    </el-form>

    <el-table
      :data="tableData"
      width="100%"
      max-height="calc(100vh - 130px)"
      border
      stripe
      row-key="fid"
      :row-style="{ height: '40px' }"
      :cell-style="{ padding: '5px' }"
      :header-cell-style="{ background: '#99CCFF', color: '#040d1c' }">
      
      <!-- 空数据状态的插槽 -->
      <template slot="empty">
        <el-empty :image-size="100" description='未查询到数据'></el-empty>
      </template>

      <el-table-column label="编号" align="center" min-width="5%">
        <template slot-scope="scope">
          {{ scope.$index + 1 }}
        </template>
      </el-table-column>
      <el-table-column prop="chain" label="链名称" min-width="15%" />
      <el-table-column label="推荐浏览器地址" min-width="25%">
        <template slot-scope="scope">
          <div v-for="(url, index) in scope.row.url" :key="url">
              <span style="font-size:14px;">推荐{{ index + 1 }}：</span>
              <a :href="url" target="_blank">{{ url }}</a>
          </div>
      </template>
      </el-table-column>
      <el-table-column label="备用浏览器地址" min-width="25%">
        <template slot-scope="scope">
          <div v-for="(url, index) in scope.row.url2" :key="url">
            <span style="font-size:14px;">备用{{ index + 1 }}：</span>
            <a :href="url" target="_blank">{{ url }}</a>
          </div>
        </template>
      </el-table-column>
      <el-table-column prop="remark" label="备注" min-width="30%" />
    </el-table>
  </div>
</template>  
  
<script>
export default {
  name: "explorer",
  data() {
    return {
      query: {
         keyword: "",
      },
      listData: [],
      tableData: [],
    };
  },
  created() {
    this.getChainList();
  },
  methods: {
    queryForm() {
      if (this.query.keyword) {
        this.tableData = this.listData.filter(item => {
          return String(item.chain).containsIgnoreCase(this.query.keyword) 
              || String(item.remark).containsIgnoreCase(this.query.keyword);
        });
      } else {
        this.tableData = this.listData;
      }
    },
    async getChainList() {
      var timestamp = new Date().getTime();
      const { data: res } = await this.$http.get(
        "static/chain_explorer.json?t=" + timestamp
      );
      //保存原始数据,后续过滤使用
      this.listData = res.listData;
      this.tableData = res.listData;
    },
  },
};
</script>  
  
<style lang="less" scoped>
.explorer {
  width: 96%;
  margin: 0 auto;
}

/deep/ .el-table__header-wrapper {
  overflow: visible !important;
}

/deep/ .el-table {
  font-size: 16px !important;
  display: flex;
  flex-direction: column;
}
</style>  



